.food-display{
    margin-top: 30px;
}
.food-display h2{
    font-weight: 500 ;
}
.food-display-list{
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(240px,1fr));
    margin-top: 30px;
    gap: 30px;
    row-gap: 50px;
}
@media (max-width:650px) {
    .food-display-list{
        grid-template-columns: repeat(2,1fr);
    }
}

@media (max-width:380px) {
    .food-display-list{
        grid-template-columns: repeat(1,1fr);
    }
}

