.food-item{
    width: 100%;
    margin: auto;
    border-radius: 15px;
    box-shadow: 0px 0px 10px #00000015;
    animation: fadeIn 1s;
}
.food-item-img{
    width: 100%;
    border-radius: 15px 15px 0px 0px;
   
}
.food-item-info{
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.food-item-name-rating{
    display: flex;
    justify-content: space-between;
}
.food-item-name-rating p{
    font-weight: 500;
    font-size: 20px;
    color: #363030;
}
.food-item-price{
    color: tomato;
    font-weight: 500;
    font-size: 20px;
}
.food-item-image-container{
    position: relative;
}
.food-item-counter,.add{
    display: flex;
    align-items: center;
    gap: 8px;
    position: absolute;
    bottom: 7px;
    right: 10px;
    cursor: pointer;
}
.food-item-counter{
    background-color: white;
    padding: 2px 3px 5px;
    border-radius: 20px;

}
.food-item-name-rating img{
    width: 30%;
}
@media(max-width:1030px){
    .food-item-img{
        width:100%;
        border-radius: 15px 15px 0px 0px;
       
    }
}

@media(max-width:565px){
    .food-item-name-rating p{
        font-size: 16px;
        color: #363030;
    }
    .food-item-name-rating img{
        width: 30%;
    }
    .food-item-desc{
        font-size: 13px;
    }
}
@media(max-width:460px){
    .food-item-name-rating p{
        font-size: 15px;
        color: #363030;
    }
    .food-item-name-rating img{
        width: 30%;
    }
    .food-item-desc{
        font-size: 13px;
    }
}
@media(max-width:444px){
    .food-item-name-rating p{
        font-size: 13px;
        color: #363030;
    }
    .food-item-name-rating img{
        width: 25%;
    }
    .food-item-desc{
        font-size: 11px;
    }
}
@media(max-width:335px){
    .food-item-name-rating p{
        font-size: 18px;
        color: #363030;
    }
    .food-item-name-rating img{
        width: 40%;
    }
    .food-item-desc{
        font-size: 14px;
    }
}
@media(max-width:290px){
    .food-item-name-rating p{
        font-size: 15px;
        color: #363030;
    }
    .food-item-desc{
        font-size: 12px;
    }
}