.footer{
    margin-top: 80px;
    background-color: rgb(46, 43, 43);
    padding: 50px ;
    
}
.footer-content{
    display: grid;
    color: white;
    grid-template-columns: repeat(3,1fr);
    gap: 80px;
}
.footer-content-left{
    display:inline-flex;
    flex-direction: column;
    min-width: 400px;
    gap: 20px;
}
.footer-content-left img{
    height: 30px;
    width: 130px;
}
.footer-logo{
    display: flex;
    gap: 10px;
}
.footer-logo img{
    height: 40px;
    width: 40px;
    cursor: pointer;
}
.footer-content-left p{
    text-align: justify;
}
.footer-content-center{
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.footer-content-center ul{
    display: flex;
    flex-direction: column;
    gap: 10px;
    list-style: none;
}
.footer-content-right{
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.horizontal-line{
    margin-top: 50px;
}
.copyright{
    text-align: center;
    color: white;
    margin-top: 20px;
}
@media (max-width:960px) {
    .footer-content-left{
        min-width: 350px;
    }
}
@media (max-width:910px) {
    .footer-content-left{
        min-width: 300px;
    }
    .footer-content{
        gap: 60px;
    }
    .footer-content-left{
        font-size: 15px;
    }
    .footer-content-center{
        font-size: 13px;
    }
    .footer-content-right{
        font-size: 13px;
    }
}
@media (max-width:760px) {
    .footer-content-left{
        min-width: 200px;
    }
    .footer-content{
        gap: 60px;
    }
    .footer-content-left{
        font-size: 15px;
    }
    .footer-content-center{
        font-size: 13px;
    }
    .footer-content-right{
        font-size: 13px;
    }
}
@media (max-width:645px) {
    .footer-content{
        display:flex;
        flex-direction: column;
    }
}