.explore-menu{
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.explore-menu h1{
    color: #262626;
}
.explore-menu-text{
    width: 60%;
}
.explore-menu-list{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    overflow-x: scroll;
}
.explore-menu-list-items{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.explore-menu-list-items img{
    width: 7.5vw;
    transition: 0.2s;
    border-radius: 50%;
    cursor: pointer;
}
.explore-menu-list-items p{
    margin-top: 10px;
    cursor: pointer;
}
.explore-menu-list-items .active{
  border: 4px solid tomato;  
}
/* For Chrome, Safari, and New Edge */
::-webkit-scrollbar {
    width: 5px; /* Width of the entire scrollbar */
    height: 5px; /* Height of the horizontal scrollbar */
  }
  ::-webkit-scrollbar-thumb {
    background: #888; /* Color of the scrollbar thumb */
    border-radius: 10px; /* Round the corners of the scrollbar thumb */
  }
  ::-webkit-scrollbar-track {
    background: #e4e3e3; /* Color of the scrollbar track */
  }
  ::-webkit-scrollbar-thumb:hover {
    background: #555; /* Color of the scrollbar thumb on hover */
  }
@media (max-width:800px){
    .explore-menu{
        gap: 15px;
    }
    .explore-menu h1{
        font-size: 30px;
    }
    .explore-menu-text{
        width: 70%;
    }
}
