
.header{
    height: 34vw;
    width: 100vw;
    margin: 30px auto;
    background: url('../../assets/header_img.png') no-repeat;
    background-size: contain;
    position: relative;
}
.header-contents{
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 1.5vw;
    max-width: 50%;
    bottom: 10%;
    left: 6vw;
    animation: fadeIn 3s;
}

.header-contents h2{
    font-weight: 500;
    color: white;
    font-size: max(4.5vw,22px   );
}
.header-contents p{
    color: white;
    font-size: 1dvw;
}
.header-contents a{
    border: none;
    color: #747474;
    font-weight: 500;
    padding: 1vw 2.3vw;
    background-color: white;
    border-radius: 50px;

}
@media (max-width:600px){
    .header-contents p{
        display: none;
    }
}
@media (max-width:400px){
    .header-contents button{
        padding: 5px 5px;
        font-size: 10px;
    }
}